import React from "react";
import Modal from "../UI/Modal/Modal";

const withErrorHandler = (WrappedComponent, Axios) => {
  return props => {
    const [state, setState] = React.useState(null);
    React.useEffect(() => {
      const responseInerceptor = Axios.interceptors.response.use(
        null,
        error => {
          setState(error);
          return Promise.reject(error);
        }
      );
      const requestInerceptor = Axios.interceptors.request.use(req => {
        setState(null);
        return req;
      });
      return () => {
        if (Axios.interceptor !== undefined) {
          Axios.interceptor.request.eject(responseInerceptor);
          Axios.interceptor.request.eject(requestInerceptor);
        }
      };
    }, []);
    const clickHandler = () => {
      setState(null);
    };
    return (
      <div>
        <Modal checkout={state} clickListener={clickHandler}>
          <p>{state ? state.message : null}</p>
        </Modal>
        <WrappedComponent {...props} />
      </div>
    );
  };
};
export default withErrorHandler;
