import React from "react";
import BC from "./BuildControl.module.css";
const buildControl = props => {
  return (
    <div className={BC.ingredient}>
      <p>{props.data.label}</p>
      <button
        className={BC.buttonClass}
        onClick={() => props.add(props.data.type)}
      >
        +
      </button>
      <button
        className={BC.buttonClass}
        onClick={() => props.remove(props.data.type)}
      >
        -
      </button>
    </div>
  );
};
export default buildControl;
