import React from "react";
import Burger from "../../Components/Burger/Burger";
import BuildControls from "../../Components/BuildControls/BuildControls";
import * as AT from "../../Store/actionTypes";
import Modal from "../../Components/UI/Modal/Modal";
import OrderSummary from "../../Components/Burger/OrderSummary/OrderSummary";
import Axios from "../../axios-database";
import Spinner from "../../Components/Spinner/Spinner";
import withErrorHandler from "../../Components/HOC/withErrorHandler";
import { connect } from "react-redux";

class BurgerBuilder extends React.Component {
  state = {
    checkout: false,
    backDropOn: false,
    loading: false
  };
  componentDidMount() {
    this.props.initializeIngredients();
  }
  setCheckout = () => {
    this.setState(state => {
      return { checkout: !state.checkout, backDropOn: !state.checkout };
    });
  };

  checkedOut = () => {
    this.props.onCheckout();
    this.clickListenerBackDrop();
  };

  clickListenerBackDrop = () => {
    this.setState({ backDropOn: false, checkout: false });
  };

  render() {
    let orderSummary = null;
    if (this.state.loading) {
      orderSummary = <Spinner />;
    }
    let BurgerManagement = <Spinner />;
    if (this.state.ingredients !== null) {
      BurgerManagement = (
        <React.Fragment>
          <Burger ingredients={this.props.ingredients}></Burger>
          <BuildControls
            add={this.props.onIngredientAdded}
            remove={this.props.onIngredientRemoved}
            price={this.props.price}
            checkout={this.setCheckout}
            disabled={!this.props.purchasable}
          ></BuildControls>
        </React.Fragment>
      );
      orderSummary = (
        <OrderSummary
          ingredients={this.props.ingredients}
          cancel={this.setCheckout}
          checkout={this.checkedOut}
          price={this.props.price}
        />
      );
    }
    return (
      <React.Fragment>
        <Modal
          checkout={this.state.checkout}
          clickListener={this.clickListenerBackDrop}
        >
          {orderSummary}
        </Modal>
        {BurgerManagement}
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    ingredients: state.ingredients,
    price: state.price,
    purchasable: state.purchasable
  };
};
const mapDispatchToProps = dispatch => {
  return {
    onIngredientAdded: ingName => dispatch(AT.addIngredient(ingName)),
    onIngredientRemoved: ingName => dispatch(AT.remIngredient(ingName)),
    onCheckout: () => dispatch(AT.checkedout()),
    initializeIngredients: () => dispatch(AT.initializeASYNC())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withErrorHandler(BurgerBuilder, Axios));
