import React from "react";
import classes from "./OrderSummary.module.css";
const orderSummary = props => {
  let ingredients = null;
  if (props.ingredients) {
    ingredients = Object.keys(props.ingredients).map((type, key) => {
      return (
        <li key={key}>
          <em style={{ textTransform: "capitalize" }}>{type}:</em>{" "}
          {props.ingredients[type]}
        </li>
      );
    });
  }
  return (
    <React.Fragment>
      <h1>Order:</h1>
      <ul>{ingredients}</ul>
      <h4>
        Price: <strong>${props.price}</strong>
      </h4>
      <div className={classes.CenteredButtons}>
        <button onClick={props.cancel} className={classes.Checkout}>
          Cancel
        </button>
        <button onClick={props.checkout} className={classes.Checkout}>
          Continue
        </button>
      </div>
    </React.Fragment>
  );
};

export default orderSummary;
